.fadeIn {
    -webkit-animation: faderIn 1.2s forwards;
            animation: faderIn 1.2s forwards;
}

.fadeOut {
    -webkit-animation: faderOut 1s forwards;
            animation: faderOut 1s forwards;
}

.fadeInSwipeLeft {
    -webkit-animation: fadeSwipeLeft 1s .5s forwards;
            animation: fadeSwipeLeft 1s .5s forwards;
}

.fadeInSwipeRight {
    -webkit-animation: fadeSwipeRight 1s .5s forwards;
            animation: fadeSwipeRight 1s .5s forwards;
}
