@-webkit-keyframes faderIn {
    0%   { opacity: 0; }
    100% { opacity: 1;}
}

@keyframes faderIn {
    0%   { opacity: 0; }
    100% { opacity: 1;}
}

@-webkit-keyframes faderOut {
    0%   { opacity: 1; }
    100% { opacity: 0; }
}

@keyframes faderOut {
    0%   { opacity: 1; }
    100% { opacity: 0; }
}

@-webkit-keyframes fadeSwipeLeft {
    0% { 
        opacity: 0;
        -webkit-transform: translate(-100%);
                transform: translate(-100%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0%);
                transform: translate(0%);
    }
}

@keyframes fadeSwipeLeft {
    0% { 
        opacity: 0;
        -webkit-transform: translate(-100%);
                transform: translate(-100%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0%);
                transform: translate(0%);
    }
}

@-webkit-keyframes fadeSwipeRight {
    0% { 
        opacity: 0;
        -webkit-transform: translate(100%);
                transform: translate(100%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0%);
                transform: translate(0%);
    }
}

@keyframes fadeSwipeRight {
    0% { 
        opacity: 0;
        -webkit-transform: translate(100%);
                transform: translate(100%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0%);
                transform: translate(0%);
    }
}
